
import {
  defineComponent,
  onMounted,
  onBeforeUnmount,
  ref,
  reactive,
  nextTick,
} from "vue";
import WangEditor from "wangeditor";

export default defineComponent({
  name: "HyEditor",
  setup() {
    const editorRef = ref<HTMLDivElement>();
    const content = reactive({
      html: "",
      text: "",
    });
    let instance: any;
    onMounted(() => {
      instance = new WangEditor(editorRef.value);
      Object.assign(instance.config, {
        onchange() {
          syncHTML();
        },
      });
      instance.config.menuTooltipPosition = "down";
      instance.config.showFullScreen = true;
      instance.create();
      // 监听屏幕大小时间
      window.onresize = () => {
        setEditorBoxSize();
      };
    });

    //组件销毁时，也及时销毁编辑器
    onBeforeUnmount(() => {
      instance.destroy();
      instance = null;
    });

    const syncHTML = () => {
      content.html = instance.txt.html();
    };
    // 设置高度
    const contentRef = ref<HTMLDivElement>();
    contentRef.value?.setAttribute;
    function setEditorBoxSize() {
      const [navMenu, bodyContent]: any = editorRef.value!.children;
      const height = contentRef.value!.offsetHeight - navMenu.offsetHeight;
      bodyContent.style.height = height + "px";
    }

    nextTick(() => {
      setEditorBoxSize();
    });
    return {
      editorRef,
      contentRef,
      content,
    };
  },
});
